.container {
    width: 100%;
    max-width: 750px;
    /* border: 1px solid rgb(185, 185, 185); */
    padding: 20px;
    background-color: rgba(8, 206, 212, 0.795);
  
    margin: 20px auto 60px;
    -webkit-box-shadow: 2px 1px 93px 38px rgba(98, 160, 208, 0.75);
    -moz-box-shadow: 2px 1px 93px 38px rgba(98, 160, 208, 0.75);
    box-shadow: 2px 1px 93px 38px rgba(98, 160, 208, 0.75);

    border-radius: 40px;
}
.cards {
  display: flex;
    justify-content: space-around;
}
.wr {
    background: rgb(1, 16, 167);
    background: linear-gradient(180deg, rgba(1, 16, 167, 1) 0%, rgba(28, 68, 153, 0.7875525210084033) 50%, rgba(21, 142, 167, 1) 100%);
    padding: 40px;
}
