.container-city {
    width: 100%;
    max-width: inherit;
    color: #2e3044;
    display: block;
    text-align: center;
    font-size: 34px;
    border-bottom: 1px solid rgb(3, 152, 197);
  

}
.container-city__title {
      margin-bottom: 20px;
}
.container-weather {
    width: 100%;
    max-width: 400px;
    /* border: 1px solid red; */
}
.container-weather img {
    width: 100%;
    max-width: 400px;
    /* min-width: 300px; */
}
.container-weather__info {
    display: block;
    text-align: center;
    font-size: 24px;
    margin: 10px 0;
}