
.container-day{
    /* border: 1px solid rgb(255, 0, 234); */
    display: flex;
    justify-content: space-around;
    padding: 20px;
    width: 100%;
    max-width: inherit;
}
.container-more {
    /* border: 1px solid rgb(24, 10, 23); */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    max-width: 400px;
}
.container-more__time {
    font-size: 54px;
    text-align: center;
    margin: 30px 0;
    width: 100%;
    max-width: 400px;
    border-bottom: 1px solid rgb(3, 152, 197);
}
.temp {
    text-align: center;
    /* border: 1px solid rgb(24, 10, 23); */
    font-size: 34px;
    color: #000;
}
.temp-max {
    font-size: 24px;
    text-align: center;
    margin: 30px 0;
}
.container-weather__info {
    font-size: 26px;
    width: 100%;
    text-align: center;
    max-width: 145px;
}